import React from "react";
import {experienceFront, experienceBackend, experienceOthers} from '@portfolio/shared/sections/experience'
import './experience.css';
import Card2 from "src/components/card2/Card2";
import {IoSettingsOutline} from "react-icons/io5";
import {MdOutlineDesktopWindows} from "react-icons/md";
import {SlGraduation} from "react-icons/sl";
import BallCanvas from "src/canvas/ball/Ball";


const Experience = () => {
  
  return (
    <section id="experience" className="experience">

      <div className="section-header">
        <h5 className="color-primary-text-dark">Co potrafię</h5>
        <h2 className="color-secondary">Doświadczenie</h2>
      </div>

      <div className="experience-cards">
        
        <Card2 delay={200} direction="right" duration={750}>
          <div className="experience-card">
            <h1 className="experience-card-header">Frontend</h1>
            <div className="experience-card-inner">
              {experienceFront.map(e => {
                return (
                  <p key={e.key}>
                    <MdOutlineDesktopWindows className='color-secondary'/> {e.key}
                  </p>
                )
              })}
            </div>
          </div>
        </Card2>

        <Card2 delay={400} direction="right" duration={750}>
          <div className="experience-card">
            <h1 className="experience-card-header">Backend</h1>
            <div className="experience-card-inner">
              {experienceBackend.map(e => {
                return (
                  <p key={e.key}>
                    <IoSettingsOutline className='color-secondary'/> {e.key}
                  </p>
                )
              })}
            </div>
          </div>
        </Card2>

        <Card2 delay={600} direction="right" duration={750}>
          <div className="experience-card">
            <h1 className="experience-card-header">Inne</h1>
            <div className="experience-card-inner">
              {experienceOthers.map(e => {
                return (
                  <p key={e.key}>
                    <SlGraduation className='color-secondary'/> {e.key}
                  </p>
                )
              })}
            </div>
          </div>
        </Card2>

      </div>

      <div className="experience-balls">
        <BallCanvas imgUrl="angular.png" />
        <BallCanvas imgUrl="react.png" />
        <BallCanvas imgUrl="html.png" />
        <BallCanvas imgUrl="css.png" />
        <BallCanvas imgUrl="typescript.png" />
        <BallCanvas imgUrl="git.png" />
      </div>
      
    </section>
  )
}

export default Experience;